/**
Fold 1
**/
.fold1 { width: 100%; height: 100vh; position: relative; overflow: hidden; }

.fold1-lft { background: url(../../images/fold1-left.jpg) no-repeat; max-height: 800px; background-size: cover; height: 100vh; }

.fold1-lft .slide-caption { border-left: 1px solid #fff; border-top: 1px solid #fff; width: 70%; }

.fold1-rht { background: url(../../images/fold1-rught.jpg) no-repeat; max-height: 800px; background-size: cover; height: 100vh; }

.fold1-rht .slide-caption { border-right: 1px solid #fff; border-bottom: 1px solid #fff; width: 60%; }

.fold1-rht .slide-caption:hover { animation-name: borderslide; animation-duration: 4s; }

.fold1 .slides-container { min-height: 800px; width: 100%; }

.fold1 .slides-container .main-slide { height: 100vh; min-height: 800px; }

.fold1 .slides-container .main-slide .item { height: 100vh; min-height: 600px; }

.fold1 .slides-container .main-slide .slide-caption { text-align: center; color: #fff; position: absolute; top: 40%; left: 0px; right: 0px; margin: 0px auto; padding: 20px; }

.fold1 .slides-container .main-slide .slide-caption h2 { font-size: 55px; font-weight: 200; margin: 0 0 15px 0; }

.fold1 .slides-container .main-slide .slide-caption h2 span { font-weight: 600; }

.fold1 .slides-container .main-slide .slide-caption p { font-size: 12px; }

.fold1 .slides-container .main-slide .slide-caption a { background: transparent; color: #fff; font-size: 14px; width: 150px; height: 45px; display: block; font-weight: 400; line-height: 45px; text-align: center; padding: 0px 0px; border: 1px solid #fff; margin: 0px auto; letter-spacing: 1px; }

.fold1 .slide-caption .hvr-bounce-to-top:before { background: #000 !important; }

.fold1 .socialtop { position: absolute; z-index: 99; top: 20px; right: 0px; width: 100px; height: 30px; }

.fold1 .socialtop ul li { display: inline-block; margin: 0 7px 0 0; }

.fold1 .socialtop ul li:last-child { margin: 0px; }

.fold1 .socialtop ul li a { color: #fff; font-size: 14px; }

