.fold2-lft .abtwrap2 img { width: 100%; margin: 80px 0px 0px 0px; }

.fold2-lft .abtwrap2 .exp { border: 5px solid #000; padding: 24px 24px 30px; width: 225px; margin: 0px auto; }

.fold2-lft .abtwrap2 .exp h4 { font-size: 80px; font-weight: 600; margin: 0px; }

.fold2-lft .abtwrap2 .exp p { font-size: 14px; text-transform: capitalize; font-weight: 400; margin: 0; }

.fold2 .abt-list { position: absolute; bottom: 14%; padding-left: 60px; }

.fold2 .abt-list1 { background-image: url("../../minified/images/interior.png"); background-position: 40px 35px; background-repeat: no-repeat; }

