.fold2 .abt-list section { padding: 30px 15px 30px 104px; background-color: #fff; cursor: pointer; -webkit-transition: background-color 2s ease-out; -moz-transition: background-color 2s ease-out; -o-transition: background-color 2s ease-out; transition: background-color 2s ease-out; }

