.services-section { width: 100%; min-height: 600px; position: relative; background: url("../../minified/images/serv-backgrnd.png") right bottom no-repeat; background-size: 50%; padding: 100px 0 100px 100px; }

.services-section-circle { border: 1px solid #e4e4e4; min-height: 500px; border-radius: 50%; width: 100%; padding: 210px 50px; height: 100vh; }

.services-section h1 { text-align: center; font-size: 4.2rem; font-weight: 100; margin: 0px 0 25px; }

.services-section figure { margin: 0px; }

.services-section figure img { margin: 0px auto; display: block; width: 90%; }

.services-section-listing { position: absolute; min-height: 400px; width: 100%; margin: 0px auto; top: 0; left: 0px; height: 100vh; }

.services-section-listing ul { width: 100%; height: 100%; position: relative; }

.services-section-listing ul li { position: absolute; list-style: none; min-width: 20px; min-height: 20px; cursor: pointer; }

.services-section-listing ul li:nth-child(1) { top: -9px; left: 0px; right: 0px; margin: 0px auto; width: 20px; height: 20px; -webkit-transition: all 1s ease-in-out; -moz-transition: all 1s ease-in-out; -ms-transition: all 1s ease-in-out; -o-transition: all 1s ease-in-out; transition: all 1s ease-in-out; }

.services-section-listing ul li:nth-child(1) p { top: -47px; left: -48px; }

.services-section-listing ul li:nth-child(2) { top: 10%; left: 0px; right: -60%; margin: 0px auto; width: 20px; height: 20px; }

.services-section-listing ul li:nth-child(2) p { top: -2px; left: 56px; }

.services-section-listing ul li:nth-child(3) { top: 40%; left: 0px; right: -95%; margin: 0px auto; width: 20px; height: 20px; }

.services-section-listing ul li:nth-child(3) p { top: -2px; left: 56px; }

.services-section-listing ul li:nth-child(4) { top: auto; bottom: 16%; left: 0px; right: -73%; margin: 0px auto; width: 20px; height: 20px; }

.services-section-listing ul li:nth-child(4) p { top: -2px; left: 56px; }

.services-section-listing ul li:nth-child(5) { top: auto; bottom: -1%; left: 0px; right: 0%; margin: 0px auto; width: 20px; height: 20px; }

.services-section-listing ul li:nth-child(5) p { top: 40px; left: -50px; }

.services-section-listing ul li:nth-child(6) { top: auto; bottom: 16%; left: -73%; right: 0%; margin: 0px auto; width: 20px; height: 20px; }

.services-section-listing ul li:nth-child(6) p { top: 0px; left: -174px; }

.services-section-listing ul li:nth-child(7) { top: 40%; bottom: auto; left: 0px; right: 95%; margin: 0px auto; width: 20px; height: 20px; }

.services-section-listing ul li:nth-child(7) p { top: 0px; left: -174px; }

.services-section-listing ul li:nth-child(8) { top: 10%; left: 0px; right: 60%; margin: 0px auto; width: 20px; height: 20px; }

.services-section-listing ul li:nth-child(8) p { top: 0px; left: -174px; }

.services-section-listing ul li span { width: 20px; height: 20px; display: block; background: #000; border-radius: 22px; }

.services-section-listing ul li img { position: absolute; top: -20px; left: -18px; opacity: 0; -moz-transform: scale(0); -webkit-transform: scale(0); -o-transform: scale(0); -ms-transform: scale(0); transform: scale(0); -webkit-transition: all 0.9s ease-in-out; -moz-transition: all 0.9s ease-in-out; -ms-transition: all 0.9s ease-in-out; -o-transition: all 0.9s ease-in-out; transition: all 0.9s ease-in-out; }

.services-section-listing ul li p { margin: 0px; position: absolute; width: 150px; display: none; -webkit-transition: all 1.5s ease-in-out; -moz-transition: all 1.5s ease-in-out; -ms-transition: all 1.5s ease-in-out; -o-transition: all 1.5s ease-in-out; transition: all 1.5s ease-in-out; }

.services-section-listing ul li:hover img, .services-section-listing ul li:hover p { display: block; opacity: 1; -moz-transform: scale(1); -webkit-transform: scale(1); -o-transform: scale(1); -ms-transform: scale(1); transform: scale(1); -webkit-transition: all 1s ease-in-out; -moz-transition: all 1s ease-in-out; -ms-transition: all 1s ease-in-out; -o-transition: all 1s ease-in-out; transition: all 1s ease-in-out; }

/***********portfolio-container*********/
.portfolio-container { width: 100%; padding: 0px 0 0px 100px; min-height: 300px; position: relative; }

/* ---- Portfolio .grid-item ---- */
/* clear fix */
.grid:after { content: ''; display: block; clear: both; }

.grid-item { box-sizing: border-box; margin-bottom: 0px; background: #fff; position: relative; min-height: 300px; overflow: hidden; float: left; }

.grid-item img { width: 100%; min-height: 300px; }

.filter-button-group { margin: 10px auto 25px; overflow: hidden; width: 450px; height: 450px; padding: 55px 0px 0px 220px; background: #fff; position: absolute; right: 0px; bottom: -25px; clip-path: circle(69% at 100% 69%); z-index: 99; }

.filter-button-group button { background: transparent; color: #000; padding: 0px; border: 0; text-transform: capitalize; font-weight: 400; font-size: 14px; display: block; float: right; text-align: right; width: 160px; margin-bottom: 20px; outline: none; cursor: pointer; }

.filter-button-group button span { padding: 5px 10px 5px 10px; }

.filter-button-group button:hover span { background: #000; color: #fff; }

.grid-item:hover .img-overlay-portfolio { cursor: pointer; -moz-transform: translateY(1px); -webkit-transform: translateY(1px); -o-transform: translateY(1px); -ms-transform: translateY(1px); transform: translateY(1px); -webkit-transition: all 1s ease-in-out; -moz-transition: all 1s ease-in-out; -ms-transition: all 1s ease-in-out; -o-transition: all 1s ease-in-out; transition: all 1s ease-in-out; }

.img-overlay-portfolio { position: absolute; background: #000; width: 100%; height: 50px; z-index: 1; text-align: center; padding: 10px 0px; color: #fff; display: block; bottom: 0px; -moz-transform: translateY(50px); -webkit-transform: translateY(50px); -o-transform: translateY(50px); -ms-transform: translateY(50px); transform: translateY(50px); -webkit-transition: all 1s ease-in-out; -moz-transition: all 1s ease-in-out; -ms-transition: all 1s ease-in-out; -o-transition: all 1s ease-in-out; transition: all 1s ease-in-out; }

.news-container { width: 100%; min-height: 400px; background-color: #f7f7f7; padding: 100px 0px; }

.news-container .news-lft { display: flex; }

.news-container .news-lft figure { margin: 0px; }

.news-container .news-container-text { font-size: 14px; font-weight: 300; }

.news-container .news-container-text h3 { font-size: 25px; font-weight: 300; letter-spacing: 2px; position: relative; margin: 0 0 30px 0; }

.news-container .news-container-text h3:after { display: block; content: ""; width: 20px; height: 5px; background: #000; left: 0px; bottom: -10px; position: relative; }

.news-container .news-container-text p { font-weight: 300; text-shadow: 0.1px 0.1px; }

.news-container .news-container-text a.readmore, .news-container .news-container-text a { background: transparent; color: #000; font-size: 14px; width: 150px; height: 45px; display: block; font-weight: 400; line-height: 45px; text-align: center; padding: 0px 0px; border: 1px solid #000; margin: 5px 0px; letter-spacing: 1px; }

.news-container .news-container-text figure img { margin: 0px auto; display: block; width: 100%; }

.contact-container { width: 100%; min-height: 400px; position: relative; padding: 50px 0px 0px; }

.contact-container:after { content: ""; display: block; background: url(../../minified/images/get-touch-bckgrnd.jpg) right bottom no-repeat; background-size: contain; position: absolute; bottom: 0px; right: 0px; width: 45%; height: 100%; z-index: -1; }

.contact-container .get-touch { clip-path: circle(51.4% at 50% 68%); background: #000000d1; width: 100%; min-height: 300px; padding: 136px 160px 15px; }

.contact-container .get-touch h2 { font-weight: 100; font-size: 55px; letter-spacing: 1.5px; color: #fff; margin: 20px 0 0; }

.contact-container .get-touch-form input { border-top: none; border-left: 0px; border-right: 0px; border-bottom: 1px solid #fff; width: 100%; padding: 15px 10px; display: block; font-size: 14px; background: transparent; margin: 0 0 10px 0px; outline: none; color: #fff; margin: 0 0 15px 0; }

.contact-container .get-touch-form input::placeholder { color: #fff; }

.contact-container .get-touch-form textarea { border-top: none; border-left: 0px; border-right: 0px; border-bottom: 1px solid #fff; width: 100%; padding: 15px 10px; display: block; font-size: 14px; background: transparent; margin: 0 0 10px 0px; outline: none; color: #fff; height: 100px; max-height: 100px; max-width: 100%; margin: 0 0 15px 0; }

.contact-container .get-touch-form textarea::placeholder { color: #fff; }

.contact-container .get-touch-form button { border: 1px solid #fff; background: transparent; color: #fff; outline: none; width: 140px; height: 40px; font-weight: 100; font-size: 14px; cursor: pointer; margin: 5px 0 5px 0; }

/**
Responsive
***/
/* ---- stats.js ---- */
.count-particles { background: #000022; position: absolute; top: 48px; left: 0; width: 80px; color: #13E8E9; font-size: 2.8em; text-align: left; text-indent: 4px; line-height: 14px; padding-bottom: 2px; font-weight: bold; }

.js-count-particles { font-size: 1.1em; }

#stats, .count-particles { -webkit-user-select: none; margin-top: 5px; margin-left: 5px; }

#stats { border-radius: 3px 3px 0 0; overflow: hidden; }

.count-particles { border-radius: 0 0 3px 3px; }

/* ---- particles.js container ---- */
#particles-js { width: 100%; height: 100vh; background-color: transparent; background-size: cover; background-position: 50% 50%; position: absolute; z-index: -1; background-repeat: no-repeat; top: 0px; }
